<template>
  <!-- åè®¡æ¶ç»ä»¶ -->
  <div class="x-time-deadline">
    <!-- è¿éæ¯æ¾ç¤ºè¿æªç»ææ¶çåå®¹ï¼è¿éåªæ¯æè¿å¾å¸å±ï¼ä½ å¯ä»¥éæã -->
    <div class="time" v-show="!isShow">
      <span class="hour">{{ myDay }}</span> :
      <span class="hour">{{ myHours }}</span> :
      <span class="minute">{{ myMinutes }}</span> :
      <span class="second">{{ mySeconds }}</span>
    </div>
    <!-- è¿éæ¯æ¾ç¤ºç»æåçåå®¹ -->
    <span class="second" v-show="isShow">{{ clocker }}</span>
  </div>
</template>
  <script>
export default {
  name: 'TimeDeadLine',
  props: {
    // æ¥æ¶ç¶ç»ä»¶ä¼ éè¿æ¥çåæ°,è¿éä¼ äº  ç»ææ¶é´ - å¼å§æ¶é´ - ç»æåæ¾ç¤ºçåå®¹
    endTime: {
      type: Number
    },
    startTime: {
      type: Number
    },
    endMsg: {
      type: String
    }
  },
  data() {
    return {
      isShow: false, // æ§å¶æ¾ç¤ºç»ææè¿æªç»ææ¾ç¤ºçåå®¹
      clocker: '', // ç»æåæ¾ç¤ºçåå®¹
      timeObj: null, // æ¶é´å¯¹è±¡,ä¸æ¹ä¼ç¨å°
      myDay: 0, // æå®ä¹æ¥æ¥æ¶è®¡ç®åºæ¥ç å¤© ç
      myHours: 0, // æå®ä¹æ¥æ¥æ¶è®¡ç®åºæ¥ç å°æ¶ ç
      myMinutes: 0, // æå®ä¹æ¥æ¥æ¶è®¡ç®åºæ¥ç åé ç
      mySeconds: 0, // æå®ä¹æ¥æ¥æ¶è®¡ç®åºæ¥ç ç§é ç,
      go: null
    }
  },
  mounted() {
    // è®¡ç®æ¶é´å·®
    let timeLag = (this.endTime - this.startTime) / 1000
    // å¤æ­å½åæ¯å¦æ¶åç§çå¼æ¯å¦å¤§äº10
    let add = num => {
      return num < 10 ? '0' + num : num
    }
    // æ¶é´åè®¡æ¶è¿ç®çæ¹æ³
    let timeFunction = () => {
      let time = timeLag--
      this.timeObj = {
        // æ¶é´å¯¹è±¡
        seconds: Math.floor(time % 60),
        minutes: Math.floor(time / 60) % 60,
        hours: Math.floor(time / 60 / 60) % 24,
        days: Math.floor(time / 60 / 60 / 24)
      }
      // è®¡ç®åºæ¶åç§
      this.myDay = `${add(this.timeObj.days)}`
      this.myHours = `${add(this.timeObj.hours)}`
      this.myMinutes = `${add(this.timeObj.minutes)}`
      this.mySeconds = `${add(this.timeObj.seconds)}`
      // å½æ¶é´å·®å°äºç­äº0æ¶,åæ­¢åè®¡æ¶
      if (time <= 0) {
        this.isShow = true
        this.clocker = this.endMsg || 'è¿å©0s'
        clearInterval(this.go)
      }
    }
    // å¼å§æ§è¡åè®¡æ¶
    timeFunction()
    // æ¯ä¸ç§æ§è¡ä¸æ¬¡
    this.go = setInterval(() => {
      timeFunction()
    }, 1000)
  },
  beforeDestroy() {
    clearInterval(this.go)
  }
}
</script>

